import axios from "axios";

const instance = axios.create({
  baseURL: "https://social-network.samuraijs.com/api/1.0/",
  withCredentials: true,
  headers: {
    "API-KEY": "5206d5ae-2797-4905-b93e-c39abf462fbf",
  },
});

export const usersAPI = {
  getUsers: (currentPage, pageSize) =>
    instance
      .get(`users?page=${currentPage}&count=${pageSize}`, {})
      .then((res) => res.data),
  follow: (userId) =>
    instance.post(`follow/${userId}`, {}).then((res) => res.data),
  unfollow: (userId) =>
    instance.delete(`follow/${userId}`, {}).then((res) => res.data),
};

export const authAPI = {
  getAuthUserData: () => instance.get(`auth/me`).then((res) => res.data),
  login: (email, password, rememberMe = false) =>
    instance
      .post(`auth/login`, { email, password, rememberMe })
      .then((res) => res.data),
  logout: () => instance.delete(`auth/login`).then((res) => res.data),
};

export const profileAPI = {
  get: (userId) => instance.get(`profile/${userId}`).then((res) => res.data),
  getStatus: (userId) =>
    instance.get(`profile/status/${userId}`).then((res) => res.data),
  updateStatus: (status) => instance.put(`profile/status`, { status }),
};
