import React from "react";
import styles from "./FormsControl.module.css";

const FormControl = ({ children, name, errors }) => {
  return (
    <div
      className={`${styles.formControl} ${errors[name] ? styles.errors : ""}`}
    >
      {children}
      {errors[name] && <p>{errors[name].message}</p>}
    </div>
  );
};

export const TextArea = ({ register, rules, name, errors, ...rest }) => {
  return (
    <FormControl name={name} errors={errors}>
      <textarea {...register(name, rules)} {...rest} />
    </FormControl>
  );
};

export const Input = ({ register, rules, name, errors, ...rest }) => {
  return (
    <FormControl name={name} errors={errors}>
      <input {...register(name, rules)} {...rest} />
    </FormControl>
  );
};

export const CreateInput =
  (register, errors) => (name, type, placeholder, fun) => {
    return (
      <Input
        name={name}
        type={type}
        register={register}
        errors={errors}
        rules={{
          validate: fun ? fun : null,
        }}
        placeholder={placeholder ? placeholder : null}
      ></Input>
    );
  };
