import { usersAPI } from "../api/api";
import { updateObjectInArray } from "../utils/objects-helpers";

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const SET_CURRENT_PAGE = "SET_CURRENT_PAGE";
const SET_TOTAL_USERS_COUNT = "SET_TOTAL_USERS_COUNT";
const TOOGLE_IS_FETCHING = "TOOGLE_IS_FETCHING";
const TOOGLE_IS_FOLLOWING_PROGRESS = "TOOGLE_IS_FOLLOWING_PROGRESS";

let initialState = {
  users: [],
  pageSize: 5,
  totalUsersCount: 0,
  currentPage: 4,
  isFetching: false,
  followingInProgress: [],
};

export function usersReducer(state = initialState, action) {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {
          followed: true,
        }),
      };
    case UNFOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {
          followed: false,
        }),
      };
    case SET_USERS:
      return { ...state, users: [...action.users] };
    case SET_CURRENT_PAGE:
      return { ...state, currentPage: action.currentPage };
    case SET_TOTAL_USERS_COUNT:
      return { ...state, totalUsersCount: action.totalUsersCount };
    case TOOGLE_IS_FETCHING:
      return { ...state, isFetching: action.isFetching };
    case TOOGLE_IS_FOLLOWING_PROGRESS: {
      return {
        ...state,
        followingInProgress: !action.isFetching
          ? state.followingInProgress.filter((id) => id !== action.userId)
          : [...state.followingInProgress, action.userId],
      };
    }
    default:
      return state;
  }
}
export let followSuccess = (userId) => ({
  type: FOLLOW,
  userId,
});

export let unfollowSuccess = (userId) => ({
  type: UNFOLLOW,
  userId,
});

export let setUsers = (users) => ({
  type: SET_USERS,
  users,
});

export let setCurrentPage = (pageNumber) => ({
  type: SET_CURRENT_PAGE,
  currentPage: pageNumber,
});

export let setTotalUsersCount = (totalUsersCount) => ({
  type: SET_TOTAL_USERS_COUNT,
  totalUsersCount,
});

export let toogleIsFetching = (isFetching) => ({
  type: TOOGLE_IS_FETCHING,
  isFetching,
});

export let toogleIsFollowingProgress = (isFetching, userId) => ({
  type: TOOGLE_IS_FOLLOWING_PROGRESS,
  isFetching,
  userId,
});

// getUsersThunkCreator;
export const requestUsers = (page, pageSize) => async (dispatch) => {
  dispatch(toogleIsFetching(true));
  dispatch(setCurrentPage(page));
  const res = await usersAPI.getUsers(page, pageSize);
  dispatch(setTotalUsersCount(res.totalCount));
  dispatch(setUsers(res.items));
  dispatch(toogleIsFetching(false));
};

const followUnfollowFlow = async (
  dispatch,
  userId,
  apiMethod,
  actionCreator
) => {
  dispatch(toogleIsFollowingProgress(true, userId));
  const data = await apiMethod;
  if (data.resultCode === 0) {
    dispatch(actionCreator(userId));
  }
  dispatch(toogleIsFollowingProgress(false, userId));
};

export const follow = (userId) => async (dispatch) => {
  let apiMethod = usersAPI.follow(userId);
  followUnfollowFlow(dispatch, userId, apiMethod, followSuccess);
};

export const unfollow = (userId) => async (dispatch) => {
  let apiMethod = usersAPI.follow(userId);
  followUnfollowFlow(dispatch, userId, apiMethod, unfollowSuccess);
};
