let mapStateToProps = (state) => {
  return {
    newPostText: state.profilePage.newPostText,
    posts: state.profilePage.posts,
  };
};
let mapDispatchToProps = (dispatch) => {
  return {
    addPost: (data) => {
      dispatch(addPostActionCreator(data));
    },
  };
};
export const PostsContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(Posts);
