import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import { Navigate } from "react-router-dom";
import { CreateInput, Input } from "../Common/FormsControls/FormsControls";
import { required } from "../../utils/validators/validators";
import { connect } from "react-redux";
import { login } from "../../redux/auth_reducer";
import style from "../Common/FormsControls/FormsControl.module.css";

export function LoginForm({ onSubmitForm }) {
  const {
    register,
    handleSubmit,
    setError,
    formState: { errors },
    reset,
  } = useForm({
    defaultValues: {
      isRememberMe: false,
    },
  });
  const createInput = CreateInput(register, errors);
  return (
    <form
      onSubmit={handleSubmit((data) => {
        onSubmitForm(data, setError);
        // if (!errors.server) {
        //   reset();
        // }
      })}
    >
      <div>{createInput("login", "text", "Login", required)}</div>
      <div>{createInput("password", "password", "Password", required)}</div>
      <div>
        {createInput("isRememberMe", "checkbox")}
        <span>remember me</span>
      </div>
      {errors.server && (
        <p className={style.formSummaryError}>{errors.server.message}</p>
      )}

      <div>
        <input type="submit" />
        Login
      </div>
    </form>
  );
}

function Login({ login, isAuth }) {
  const handleSubmit = (formData, setError) => {
    login(formData.login, formData.password, formData.isRememberMe, setError);
  };
  if (isAuth) {
    return <Navigate to="/profile" />;
  }
  return (
    <div>
      <h1>Login</h1>
      <LoginForm onSubmitForm={handleSubmit} />
    </div>
  );
}
const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
});
export default connect(mapStateToProps, { login })(Login);
