import { profileAPI } from "../api/api";

const ADD_POST = "ADD-POST";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_USER_STATUS = "SET_USER_STATUS";
const DELETE_POST = "DELETE_POST";

let initialState = {
  posts: [
    { id: 1, message: "Hi, how are you? ", likeCount: 1 },
    { id: 2, message: "It's my first post", likeCount: 5 },
    { id: 3, message: "It's my second post", likeCount: 2 },
  ],
  newPostText: "it-kamasutra.com",
  profile: null,
  status: null,
};

export function profileReducer(state = initialState, action) {
  switch (action.type) {
    case ADD_POST:
      let newPost = {
        id: state.posts.length + 1,
        message: action.post,
        likeCount: 0,
      };
      return {
        ...state,
        posts: [...state.posts, newPost],
        newPostText: "",
      };
    case SET_USER_PROFILE:
      return {
        ...state,
        profile: action.profile,
      };
    case SET_USER_STATUS:
      return {
        ...state,
        status: action.status,
      };
    case DELETE_POST:
      return {
        ...state,
        posts: state.posts.filter((post) => post.id != action.postId),
      };
    default:
      return state;
  }
}

export let addPostActionCreator = (data) => ({
  type: ADD_POST,
  post: data.post,
});
export let deletePostActionCreator = (postId) => ({
  type: DELETE_POST,
  postId,
});

let setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile });
let setUserStatus = (status) => ({ type: SET_USER_STATUS, status });

export const getProfile = (userId) => async (dispatch) => {
  const data = await profileAPI.get(userId);
  dispatch(setUserProfile(data));
};

export const getUserStatus = (userId) => async (dispatch) => {
  const data = await profileAPI.getStatus(userId);
  dispatch(setUserStatus(data));
};

export const updateUserStatus = (status) => async (dispatch) => {
  const res = await profileAPI.updateStatus(status);
  if (res.data.resultCode === 0) {
    dispatch(setUserStatus(status));
  }
};
