const SEND_MESSAGE = "SEND-MESSAGE";

let initialState = {
  dialogs: [
    { id: 1, name: "Olya" },
    { id: 2, name: "Alina" },
    { id: 3, name: "Oleg" },
  ],
  messages: [
    {
      id: 1,
      message: "Hi",
    },
    {
      id: 2,
      message: "Hello",
    },
    {
      id: 3,
      message: "By",
    },
  ],
  newMessageText: "",
};

export function messagesReducer(state = initialState, action) {
  switch (action.type) {
    case SEND_MESSAGE:
      return {
        ...state,
        messages: [
          ...state.messages,
          {
            id: state.messages.length + 1,
            message: action.message,
          },
        ],
        newMessageText: "",
      };
    default:
      return state;
  }
}

export let sendMessageActionCreator = (message) => ({
  type: SEND_MESSAGE,
  message,
});
