import React, { useState } from "react";
import styles from "./Pagination.module.css";

export function Pagination({
  totalUsersCount,
  pageSize,
  currentPage,
  onPageChanged,
  portionSize = 10,
}) {
  let pagesCount = Math.ceil(totalUsersCount / pageSize);
  let pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }
  let portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftProtionPageNumber = (portionNumber - 1) * portionSize + 1;
  let rightProtionPageNumber = portionNumber * portionSize;

  return (
    <div>
      {portionNumber > 1 && (
        <button
          onClick={() => {
            setPortionNumber(portionNumber - 1);
          }}
        >
          PREV
        </button>
      )}
      {pages
        .filter(
          (p) => p >= leftProtionPageNumber && p <= rightProtionPageNumber
        )

        .map((page) => (
          <span
            onClick={() => onPageChanged(page)}
            className={currentPage === page && styles.selectedPage}
          >
            <span className={styles.page}> {page}</span>
          </span>
        ))}
      {portionCount > portionNumber && (
        <button
          onClick={() => {
            setPortionNumber(portionNumber + 1);
          }}
        >
          NEXT
        </button>
      )}
    </div>
  );
}
